<template>
  <div class="about">
    <h1>{{id? '': ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="" >
        <el-input placeholder="" v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-button size="small" @click="model.items.push({})">
          <i class="el-icon-plus"></i>
        </el-button>
        <el-row type="flex" style="flex-wrap: wrap">
          <el-col :md="24" v-for="(item, i) of model.items" :key="i">
            <el-form-item label="URL">
              <el-input v-model="item.url"></el-input>
            </el-form-item>
            <el-form-item label="" style="margin-top: 0.5rem">
              <el-upload
              class="avatar-uploader"
              :action="mixUploadUrl"
              :headers ="mixGetAuthHeaders()"
              :show-file-list="false"
              :on-success="res => $set(item, 'image', res.url)">
              <img v-if="item.image" :src="item.image" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
            </el-form-item>
            <el-form-item>
              <el-button type="danger" size="small" 
                @click="model.items.splice(i, 1)">
                
              </el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit" round></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data () {
    return {
      model: {
        items: []
      },
    }
  },
  methods: {
    async save() {
      // this.$http.post('ads', this.model).then()
      let res
      if (this.id) {
        console.log('this.id and $route.params.id ', this.id, this.$route.params.id)
        res = await this.$http.put(`rest/ads/${this.id}`, this.model)
        res
      } else {
        // this.modelaxiosreq.bodyexpressmongodb
        res = await this.$http.post('rest/ads', this.model)
        res
      }
      this.$router.push('/ads/list')
      this.$message({
        type: 'success',
        message: ''
      })
    },
    // 
    async fetch () {
      // populate_idnameidparentparent
      const res = await this.$http.get(`rest/ads/${this.id}`)
      // this.model = res.data
      this.model = Object.assign({}, this.model, res.data)
    }
  },
  created () {
    // idmongodb
    this.id && this.fetch()
  }
}
</script>

