<template>
  <div class="about">
    <h1>{{id? '': ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="" >
        <el-select placeholder="" v-model="model.categories" multiple filterable style="width: 100%">
          <el-option
            v-for="item of categories" :key="item._id"
            :label="item.name" :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" >
        <el-input placeholder="" v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="" >
        <!-- <el-input type="textarea" placeholder="" v-model="model.body"></el-input> -->
        <vue-editor v-model="model.body"
          useCustomImageHandler @image-added="handleImageAdded"
        ></vue-editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit" round></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor";
// import axios from "axios";

export default {
  components: {
    VueEditor
  },
  props: {
    id: {}
  },
  data () {
    return {
      model: {},
      categories: []
    }
  },
  methods: {
    async save() {
      // this.$http.post('categories', this.model).then()
      let res
      if (this.id) {
        res = await this.$http.put(`rest/articles/${this.id}`, this.model)
        res
      } else {
        // this.modelaxiosreq.bodyexpressmongodb
        res = await this.$http.post('rest/articles', this.model)
        res
      }
      this.$router.push('/articles/list')
      this.$message({
        type: 'success',
        message: ''
      })
    },
    // 
    async fetch () {
      const res = await this.$http.get(`rest/articles/${this.id}`)
      this.model = res.data
    },
    // 
    async fetchCategories () {
      const res = await this.$http.get(`rest/categories`)
      this.categories = res.data
    },
    async handleImageAdded (file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData();
      formData.append("file", file);

      const res = await this.$http.post('upload', formData)
      Editor.insertEmbed(cursorLocation, "image", res.data.url);
      resetUploader();
      // axios({
      //   url: "https://fakeapi.yoursite.com/images",
      //   method: "POST",
      //   data: formData
      // })
      //   .then(result => {
      //     let url = result.data.url; // Get url from response
      //     Editor.insertEmbed(cursorLocation, "image", url);
      //     resetUploader();
      //   })
      //   .catch(err => {
      //     console.log(err);
      //   });
    }
  },
  created () {
    this.fetchCategories()
    // idmongodb
    this.id && this.fetch()
  }
}
</script>

