<template>
  <div>
    <h1></h1>
    <!-- itemsitemitem_id, parent, name -->
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="230px"></el-table-column>
      <!-- <el-table-column prop="parent" label=""></el-table-column> -->
      <el-table-column prop="parent.name" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="180">
        
        <template v-slot="scope">
          <!-- {{scope.row}} -->
          <el-button type="text" size="small" @click="$router.push(`/categories/edit/${scope.row._id}`)"></el-button>
          <el-button type="text" size="small" @click="remove(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      items: []
    }
  },
  methods: {
    async fetch () {
      const res = await this.$http.get('rest/categories')
      this.items = res.data
      console.log('this.items is ', this.items)
    },
    async remove (row) {
      // $confirm element
      this.$confirm(`"${row.name}"`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const res = await this.$http.delete(`rest/categories/${row._id}`)
        res
        this.$message({
          type: 'success',
          message: '!'
        });
        this.fetch(); // 
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });          
      });
    }
  },
  created () {
    this.fetch()
  }
}
</script>
