<template>
  <div class="about">
    <h1>{{id? '': ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="" >
        <!-- inputmodel -->
        <el-input placeholder="" v-model="model.username"></el-input>
      </el-form-item>
      <el-form-item label="" >
        <!-- inputmodel -->
        <el-input type="text" placeholder="" v-model="model.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit" round></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data () {
    return {
      model: {},
    }
  },
  methods: {
    async save() {
      // this.$http.post('admin_users', this.model).then()
      let res
      if (this.id) {
        console.log('this.id and $route.params.id ', this.id, this.$route.params.id)
        res = await this.$http.put(`rest/admin_users/${this.id}`, this.model)
        res
      } else {
        // this.modelaxiosreq.bodyexpressmongodb
        res = await this.$http.post('rest/admin_users', this.model)
        res
      }
      this.$router.push('/admin_users/list')
      this.$message({
        type: 'success',
        message: ''
      })
    },
    // 
    async fetch () {
      // populate_idnameidparentparent
      const res = await this.$http.get(`rest/admin_users/${this.id}`)
      console.log('res is ', res)
      this.model = res.data
      console.log('this.model is ', this.model)
    }
  },
  created () {
    // idmongodb
    this.id && this.fetch()
  }
}
</script>

